.conventions {
	&__inner {
		@include sm {
			padding: 0; } }
	&__main {
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start; }
	&__gallery {
		width: 50%;
		@include lg {
			width: 100%; } }
	&__text {
		width: 50%;
		padding-left: 50px;
		@include lg {
			width: 100%;
			padding-left: 0;
			margin-top: 40px; }
		@include sm {
			padding: 0 16px; } }
	&__descr {
		margin-top: 30px;
		line-height: 2;
		@include sm {
			margin-top: 24px; } } }
